<template>
  <div>
    <table>
      <tr><th>学院</th><th>点星数</th></tr>
      <tbody>
  <tr v-for="(item,index) in articles" :key="index" >
     <td >{{item.collegename}}</td>
     <td >{{item.star}}</td>
  </tr>
      </tbody>
    </table>
  </div>
</template>

<script>
    export default {
        name: "content4",
      name: "content3",
      data(){
        return{
          articles:[]}
      },
      watch:{
        'AAA':{
          immediate:true,
          deep:true,
          handler:function () {
            this.$http.get('https://api.pomelo072.top/count/collegeList')

              .then(e =>{
                let data=e.data
                if (data.Data && Array.isArray(data.Data)){
                  this.articles=data.Data
                }
                console.log(this.articles)
              })
              .catch(err => {console.log(err)})
          }

        }}
    }
</script>

<style scoped>

</style>



