            let data=e.data
            this.star=data.Data
          })
          .catch(err => {console.log(err)})
        this.$http.get('https://api.pomelo072.top/msg/list',{params:{type:"well",
            pages:"1",
            pagesize:"60"}})
          .then(e =>{
            let data=e.data
            if(data.Data && Array.isArray(data.Data)){
              this.msg1=e.data.Data}
            console.log(this.msg1)
          })
          .catch(err => {console.log(err)})
        this.$http.get('https://api.pomelo072.top/stars/list')
          .then(e =>{
            let data=e.data

          })
          .catch(err => {console.log(err)})

      },
      del(e){
        console.log(e)
        var index=e.path[2].rowIndex;
        console.log(index)
        var table = document.getElementById("table1");
        console.log(table)
      table.deleteRow(index)
      var id=e.toElement.id;
   var time=e.toElement.className;
    var delid=e.path[2].className;
     var table = document.getElementById("table");
        var len = table.rows.length;
      console.log(len)
       console.log(delid)
      this.$http.get('https://api.pomelo072.top/msg/delete' ,{params:{msgid:id}})
        .then(e =>{
         let data=e.data
           console.log(e)
        console.log(data)


           })
          .catch(err => {console.log(err)})

      }},
      watch:{
        'AAA':{
          immediate:true,
          deep:true ,
          handler:function () {
            this.$http.get('https://api.pomelo072.top/count/total')
              .then(e =>{
                let data=e.data
                this.star=data.Data
              })
              .catch(err => {console.log(err)})
          }
        },
        "ccc":{
          immediate:true,
          deep:true ,
          handler:function () {
            this.$http.get('https://api.pomelo072.top/stars/list')
              .then(e =>{
                let data=e.data

                this.msg=this.msg.concat(e.data.Data)

              })
              .catch(err => {console.log(err)})
          }
        },
        "bbb":{
          immediate:true,
          deep:true ,
          handler:function () {
            this.$http.get('https://api.pomelo072.top/msg/list',{params:{type:"well",
              pages:"1",
              pagesize:"60"}})
              .then(e =>{
                let data=e.data
                if(data.Data && Array.isArray(data.Data)){
                this.msg1=e.data.Data}
                console.log(this.msg1)
              })
              .catch(err => {console.log(err)})
          }
        },
        "bbb1":{
          immediate:true,
          deep:true ,
          handler:function () {
            this.$http.get('https://api.pomelo072.top/msg/list',{params:{type:"well",
                pages:"2",
                pagesize:"60"}})
              .then(e =>{
                let data=e.data
                if(data.Data && Array.isArray(data.Data)){
                  this.msg1=this.msg1.concat(e.data.Data)}
                console.log(this.msg1)
              })
              .catch(err => {console.log(err)})
          }
        },
        "bbb2":{
          immediate:true,
          deep:true ,
          handler:function () {
            this.$http.get('https://api.pomelo072.top/msg/list',{params:{type:"well",
                pages:"3",
                pagesize:"60"}})
              .then(e =>{
                let data=e.data
                if(data){
                  this.msg1=this.msg1.concat(e.data.Data)}
                console.log(this.msg1)
              })
              .catch(err => {console.log(err)})
          }
        },
        "bbb3":{
          immediate:true,
          deep:true ,
          handler:function () {
            this.$http.get('https://api.pomelo072.top/msg/list',{params:{type:"well",
                pages:"4",
                pagesize:"60"}})
              .then(e =>{
                let data=e.data
                if(data.Data && Array.isArray(data.Data)){
                  this.msg1=this.msg1.concat(e.data.Data)}
                console.log(this.msg1)
              })
              .catch(err => {console.log(err)})
          }
        },
        "bbb4":{
          immediate:true,
          deep:true ,
          handler:function () {
            this.$http.get('https://api.pomelo072.top/msg/list',{params:{type:"well",
                pages:"5",
                pagesize:"60"}})
              .then(e =>{
                let data=e.data
                if(data.Data && Array.isArray(data.Data)){
                  this.msg1=this.msg1.concat(e.data.Data)}
                  console.log(this.msg1)
              })
              .catch(err => {console.log(err)})
          }
        }
      }
  }
</script>



