<template>
  <div>
    <div style="position: relative;left: 2vw">
    <p style="font-size: 30px;font-weight: bolder;position: absolute;left: 18vw;top:8vh">待审核留言</p>
    </div>
      <div  class="scol1" style="overflow-y:scroll;height: 30vw;overflow-x:auto;position: absolute;top: 20vh;left: 20vw;">
        <table  border="1" class="table" style="width:60vw" id="table1">
          <th>姓名</th>
          <th>留言内容</th>
          <th>日期</th>
        <tbody valign="middle">
        <tr v-for="(item,index) in articles" :key="index" >
          <td >{{item.replyname}}</td>
          <td >{{item.replymsg}}</td>
          <td >{{item.replytime}}</td>
          <td><button class="suc"  @click="c1" :id="item.msg_id">审核通过</button></td>
          <td><button  @click="c2" :id="item.msg_id">审核失败</button></td>
        </tr>
        </tbody>
        </table>
    </div>
  </div>
</template>
<style scoped>
  .scol{width: 32vw;margin: 0;border-collapse: collapse;position: absolute;left: 30%;top: 13vw;height: 100px;overflow: scroll}
  *{margin: 0;padding: 0;font-size: 100%;}
  .bt{outline: none;width: 8vw;font-size: 15px;border-color: rgb(247,247,247);border-radius: 100px;position: absolute;top:9.5vh;text-decoration-line: none}

  tr:nth-child(even){background-color: white;}
  tr{text-align: center;cursor: pointer;}
  tr:hover{background-color: darkgray;}
  td{border:solid 1px rgb(44,44,44);}
</style>
<script>
  export default {
    name: "content3",
    data(){
      return{
      articles:[]}
    },
    methods:{
      c1(res){
        var table=document.getElementById("table1");
        var index=res.path[2].rowIndex;
        table.deleteRow(index)
        var id=res.toElement.id
        this.$http.get('https://api.pomelo072.top/msg/reviewPass' ,{params:{msgid:id}})
        .then(e =>{
          let data=e.data
          if (data.data && Array.isArray(data.data)){
            table1.rows[id].removeNode()
          }
        })
        .catch(err => {console.log(err)})
      },
      c2(res){
        var table=document.getElementById("table1");
        var index=res.path[2].rowIndex;
        table.deleteRow(index)
        console.log(res)
        var id=res.toElement.id
        console.log(id)
        this.$http.get('https://api.pomelo072.top/msg/reviewdel' ,{params:{msgid:id}})
          .then(e =>{
            let data=e.data
            if (data.data && Array.isArray(data.data)){

            }
          })
          .catch(err => {console.log(err)})
      }},
    watch:{
      'AAA':{
        immediate:true,
        deep:true,
        handler:function () {
        this.$http.get('https://api.pomelo072.top/msg/reviewlist')

          .then(e =>{
            let data=e.data
            console.log(e.data)
            if (data.Data && Array.isArray(data.Data)){
              this.articles=data.Data
            }
            console.log(this.articles)
            console.log(this.articles[0].msg_id)
          })
          .catch(err => {console.log(err)})
      }

      }}
  }
</script>



